import {
  AREA_LIST_KNOWN,
  STATION_HEIGHT_LIST_KNOWN,
  STATION_IS_ADDING_UPDATE,
  STATION_LIST_APPENDED,
  STATION_LIST_KNOWN,
  STATION_WEATHER_LIST_KNOWN,
} from '../actions/stationActions';

const InitialState = {
  areaList: [],
  stationList: [],
  newStation: {
    isAdding: false,
    lat: null,
    lng: null,
  },
  stationData: {
    heightList: [],
    weatherList: [],
  },
};

export default function reducer(state = InitialState, action: any) {
  switch (action.type) {
    case AREA_LIST_KNOWN:
      return {
        ...state,
        areaList: action.areaList,
      };
    case STATION_LIST_KNOWN:
      return {
        ...state,
        stationList: action.stationList,
      };
    case STATION_LIST_APPENDED:
      return {
        ...state,
        stationList: [...state.stationList.slice(0), action.station],
      };
    case STATION_WEATHER_LIST_KNOWN:
      return {
        ...state,
        stationData: {
          ...state.stationData,
          weatherList: action.weatherList,
        },
      };
    case STATION_HEIGHT_LIST_KNOWN:
      return {
        ...state,
        stationData: {
          ...state.stationData,
          heightList: action.dataList,
        },
      };
    case STATION_IS_ADDING_UPDATE:
      return {
        ...state,
        newStation: {
          isAdding: action.isAdding,
          lat: action.lat,
          lng: action.lng,
        },
      };

    default:
  }
  return state;
}
