import ApolloClient, { gql } from 'apollo-boost';
import fetch from 'isomorphic-fetch';

export const client = new ApolloClient({
  uri: 'http://api.sewage.gnometech.no/graphql',
  // uri: 'http://localhost:8090/graphql',
  fetch,
});

export const query: Function = (gqlString: string, variables: object = {}) =>
  new Promise((resolve, reject) => {
    client
      .query({
        query: gql`
          ${gqlString}
        `,
        variables,
      })
      .then(result => {
        if (result && result.data) {
          resolve(result.data);
        }
        reject(new Error('Query result data is empty.'));
      })
      .catch(error => {
        reject(new Error(error.message || error));
      });
  });

export const mutate: Function = (gqlString: string, variables: object = {}) =>
  new Promise((resolve, reject) => {
    client
      .mutate({
        mutation: gql`
          ${gqlString}
        `,
        variables,
      })
      .then(result => {
        if (result && result.data) {
          resolve(result.data);
        }
        reject(new Error('Query result data is empty.'));
      })
      .catch(error => {
        reject(new Error(error.message || error));
      });
  });
